.container {
  height: 90px;
  width: 225px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  display: flex;
  flex-direction: row;
  justify-content: end;
  align-items: center;
}

.github-logo {
  height: 70px;
  width: 70px;
  background-color: white;
  border-radius: 100%;
}

.github-img {
  height: 100%;
  width: 100%;
}

.speech-bubble {
  display: flex;
  align-items: center;
  background-color: white;
  height: 50px;
  margin-right: 8px;
  text-align: center;
  border-radius: 10px;
  border-style: solid;
  border-width: 3px;
  border-color: black;
  animation: fadeIn 1s;
}

.hidden {
  visibility: hidden;
}

@keyframes fadeIn {
  0% { opacity: 0;}
  100% {opacity: 1;}
}